<template>
  <!--首页-->
  <div id="home" class="left">
    <div class="home-header" v-if="false">
      <div class="header-classify" @click="hrefName('classify')"><i class="iconfont icon-fenlei"></i></div>
      <div class="header-search" @click="$router.push({path:'/search'})">
        请输入搜索内容
      </div>
      <div class="header-message" @click="hrefName('me')"><i class="iconfont icon-icon--"></i></div>
    </div>
    <main class="home-main">
      <!--banner广告-->
      <div class="home-main-banner">
        <mt-swipe :auto="0" v-if="bannerList">
          <mt-swipe-item v-for="(item,index) in bannerList" :key="index" v-if="index<6">
            <img :src="imgHost+item.img" :title="item.title" @click="$router.push({name:'proDetail',query:{proId:item.id}})">
          </mt-swipe-item>
        </mt-swipe>
      </div>
      <!--导航-->
      <div class="home-main-nav">
        <div class="home-nav home-nav-classify" @click="hrefName('classify')">
          <i class="iconfont"></i>
          全部分类
        </div>
        <div class="home-nav home-nav-order" @click="$router.push({path:'/myOrder',query:{title:'全部订单'}})">
          <i class="iconfont"></i>
          我的订单
        </div>
        <div class="home-nav home-nav-shopcart" @click="hrefName('shopcart')">
          <i class="iconfont"></i>
          购物车
        </div>
        <div class="home-nav home-nav-me" @click="hrefName('me')">
          <i class="iconfont"></i>
          个人中心
        </div>
      </div>
      <!--精品广告-->
      <div class="home-main-boutique" v-if="boutiqueList">
        <div class="home-boutique-image" v-for="(item,index) in boutiqueList" :key="index"
             @click="$router.push({path:'/proDetail',query:{proId: item.id}})" v-if="index < 8">
          <img :src="imgHost+item.productImageLink" :alt="item.productName">
        </div>
      </div>
      <!--猜你喜欢-->
      <div class="home-main-like" v-if="likeList">
        <div class="home-like-title" v-if="false">
          <h2>猜你喜欢</h2>
        </div>
        <div class="home-like-list clear">
          <div class="home-like-li" v-for="(item,index) in likeList" :key="index"
               @click="$router.push({path:'/proDetail',query:{proId: item.id}})" v-if="index < 8">
            <div class="home-like-image">
              <img :src="imgHost+item.productImageLink" alt="">
            </div>
            <div class="home-like-name">
              {{item.productName}}
            </div>
            <div class="home-like-money">
              ￥{{item.salesPrice}}元
            </div>
          </div>
        </div>
      </div>
    </main>

  </div>
</template>

<script>
  import submitBtn from '../../components/button/submitBtn.vue'
  import payPopup from '../../components/pay/payPopup.vue'


  export default {
    data() {
      return {
        bannerList: null,
        boutiqueList: null,
        likeList: null,
        active: 1,
        cardNumber: '',
        agreementActive: false,
        showWarn: false,
        showWarnMoney: false,
        cardNumber2: '',
        showWarn2: false,
      }
    },
    computed: {},
    components: {
      submitBtn,
      payPopup
    },
    created() {
      this.getBan();//定义获取图片方法
      this.getExtend();
    },
    methods: {
      getBan() {
        var that = this;
        that.ajax({
          url: '/ad/homepage',
          success: function (res) {
            console.log(res);
            that.bannerList = res;
          },
        })
      },
      getExtend() {
        let that = this;
        that.ajax({
          url: '/product/recommendedList?cityName',
          success: function (res) {
            that.likeList = res.data;
          },
        })
      },
      sliceCard(val, num) {
        if (num) {
          if (val.length > 19) {
            this.cardNumber2 = val.slice(0, 19);
            this.showWarn2 = false;
          }
        } else {
          if (val.length > 19) {
            this.cardNumber = val.slice(0, 19);
            this.showWarn = false;
          }
        }

      },
      blurCard(val) {
        if (val.length == 19) {
          this.showWarn = false;
        } else {
          this.showWarn = true;
        }
      },
      sliceMoney(val) {
        if (val % 100 == 0) {
          this.showWarnMoney = false;
        }
      },
      blurMoney(val) {
        if (val % 100 == 0) {
          this.showWarnMoney = false;
        } else {
          this.showWarnMoney = true;
        }
      },
      submitBtn() {
        let that = this;
        if (that.agreementActive && !that.showWarn && that.cardNumber != '') {
          this.popupVisible = true;
        }
      },
      submitBtn2() {
        let that = this;
        if (that.agreementActive2 && !that.showWarn2 && that.cardNumber2 != '') {
          this.popupVisible = true;
        }
      }
    },
    mounted() {

    }
  }
</script>

<style lang="scss" scoped>
  @import "../../style/minxin";

  #home {
    width: 100vw;
    .home-header {
      position: absolute;
      z-index: 999;
      left: 0;
      right: 0;
      .header-classify {
        display: block;
        float: left;
        padding: 0 10px;
        .iconfont {
          @include sc($max, $f);
          line-height: $top;
        }
      }
      .header-search {
        margin: 5px 0;
        @include cen;
        background: rgba(255, 255, 255, .3);
        border-radius: $top;
        width: 75%;
        text-align: center;
        @include sh($med, $top - 0.6);
        color: rgba(255, 255, 255, .6);
      }
      .header-message {
        display: block;
        float: right;
        padding: 0 10px;
        .iconfont {
          @include sc($max, $f);
          line-height: $top;
        }
      }
    }

  }

  .home-main {
    padding: 0 0 ($bottom + 2);
    /*banner*/
    .home-main-banner {
      height: 10rem;
      img {
        @include wh(100%, 100%);
      }
    }
    /*导航*/
    .home-main-nav {
      @include wh(100%, 5.28rem);
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      background: $f;
      .home-nav {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: $min;
        .iconfont {
          display: block;
          @include wh(3rem, 3rem);
        }
      }
      .home-nav-classify .iconfont {
        @include bg('../../assets/img/nav/nav_0.png');
      }
      .home-nav-order .iconfont {
        @include bg('../../assets/img/nav/nav_1.png');
      }
      .home-nav-shopcart .iconfont {
        @include bg('../../assets/img/nav/nav_7.png');
      }
      .home-nav-me .iconfont {
        @include bg('../../assets/img/nav/nav_5.png');
      }
    }
    /*精品广告*/
    .home-main-boutique {
      margin: 10px 0;
      overflow: hidden;
      .home-boutique-image {
        width: 50%;
        height: 10rem;
        float: left;
        img {
          width: 100%;
          height: 10rem;
        }
      }
    }
    /*猜你喜欢*/
    .home-main-like {
      .home-like-title {
        text-align: center;
        background-image: url('../../assets/img/bejing.png');
        background-size: contain;
        height: 2rem;
        h2 {
          @include sh($med, 2rem);
          color: $gray;
        }
      }
      .home-like-list {
        width: 100%;
        padding: 0 5px;
      }
      .home-like-li {
        width: 50%;
        float: left;
        padding: 10px 5px;
        .home-like-image {
          width: 100%;
          background: $f;
          img {
            width: 100%;
          }
        }
        .home-like-name {
          display: block;
          @include sh($med, $med +0.4);
          height: $med*3;
          overflow: hidden;
          background: $f;
          padding: 5px 6px 0;
          @include el(2);
        }
        .home-like-money {
          @include sc($med, $red);
          background: $f;
          padding: 5px;
        }
      }

    }
  }

  .home-tab-bottom {
    margin-top: 0.5rem;
    .home-money, .home-items {
      padding: 1rem;
      background: $f;
    }
    .home-money {
      .home-money-list {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        .home-money-li {
          margin-left: 1rem;
          padding: 0 1rem;
          @include sh($med, 2.2rem);
          border: 1px solid $border;
          border-radius: 5px;
          &:first-child {
            margin-left: 0;
          }
          span {
            font-size: 0.6rem;
          }
        }
        .sinopecActive {
          border: 1px solid $blue;
        }

      }
      .home-card {
        padding: 1rem 0 2rem;
        position: relative;
        .home-card-img {
          position: relative;
          &:after {
            @include mid;
            display: block;
            content: '';
            @include wh(1.2em, 1rem);
            background: url("../../assets/img/fuelcard_rech_img.png") no-repeat left top;
            background-size: 100% 100%;
          }
        }
        .home-card-input {
          @include wh(100%, 2.48rem);
          padding-left: 3rem;
          border-bottom: 2px solid $blue;

        }
        .card-input-warn {
          position: absolute;
          @include wh(100%, 2rem);
          padding-left: 3rem;
          left: 0;
          botto: 0;
        }
      }
      .home-money-other {
        width: 100%;
        padding: 2rem 0 1rem;
        .home-money-input {
          @include wh(100%, 2.48rem);
          padding-left: 3rem;
          border-bottom: 2px solid $border;
        }
        .money-input-warn {
          padding-left: 3rem;
          @include wh(100%, 2rem);
        }
      }

      .money-other-img {
        position: relative;
        &:after {
          @include mid;
          display: block;
          content: '￥';
          @include wh(1.2rem, 2.48rem);
          @include sh(1.2rem, 2.48rem);
          color: #bbb;
        }

      }
      input {
        display: block;
        &::placeholder {
          color: #bbb;
        }
      }

    }
    .home-items {
      margin-top: 0.5rem;
      p {
        @include sh(0.78rem, 1.56rem);
        color: $grey;
        padding-left: 1rem;
      }
    }
    .home-agreement {
      @include wh(100%, 4rem);
      padding: 1rem 1rem 2rem;
      @include sh(0.93rem, 1.1rem);
      @include fj;
      flex-direction: row;
      position: relative;
      p {
        display: flex;
        flex-direction: row;
        span {
          padding-left: 0.3rem;
        }
      }
      i {
        display: block;
        @include wh(1.1rem, 1.1rem);
        background: url("../../assets/img/newcircle.png") no-repeat left top;
        background-size: 100% 100%;

      }
      .agreementImg {
        background-image: url("../../assets/img/newcircle2.png");
      }
      b {
        color: $blue;
      }
    }
  }
</style>



